var fetch_api_call = function(url,query) {
    const token = localStorage.getItem('Authorization')

    var response = fetch(url, {
        method: 'POST',
        headers: {
          'content_type': 'applcation/json',
          'Authorization': token,
        },
        body:  JSON.stringify(
          {
            query: query
          }
        )  
      })

    return response
}
   
exports.fetch_api_call = fetch_api_call;