
import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';
 
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import ChartistGraph from 'react-chartist';
import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';
 
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';
import ErrorComponent from '../components/ErrorComponent';
import { fetch_api_call } from '../utils/GraphQl';


class Metrics extends React.Component {
  constructor(props){
    super(props);
 
    this.state  = {
      metrics: [],
      error:'',
      error_code:0,
    }
  }
 
  async fetchEtlFlowMetrics() {
    const GRAPHQL_URL = this.props.GRAPHQL_URL
 
    this.setState(prevState => ({
      loader: true,
      error: ''
    }))
    
    var body = ` query { metrics { active_jobs  active_subscribers etl_jobs cron_jobs  build_time } }`
    
    const response = await fetch_api_call(GRAPHQL_URL,body)  
 
    if (response.status === 403) {
      this.props.logout() 
      localStorage.removeItem("Authorization")
    } 

    if (!response.ok) {
      var message = `An error has occured: ${response.statusText}`;
      this.setState(prevState => ({
        error_code: response.status
      }))
      throw  new Error(message);
    } 
    else {  
      const {data} = await response.json();
      return data; 
    }  
  }
 
  componentDidMount() {
    this.setMetrics()
  }
 
  setMetrics(){
    this.fetchEtlFlowMetrics().then(({metrics}) => {
      this.setState(prevState => ({
          metrics: metrics
      }))
    }).catch(error => {
      this.setState(prevState => ({
        error: error,
        loader: false
      }))
  });  
  }
 
  render(){
 
    if (this.state.error !== ''){
      return (
        <div class="container">
         <ul class = "pagination justify-content-center">
             <button class= "btn btn btn-sm" type="button " onClick = {() => {
                    this.setMetrics()
                }}>
               Refresh
             </button>
         </ul>
            <ErrorComponent error={this.state.error_code}/>
        </div>
      );
     } else {
      return (
      <div class="card">
      <div class="card-header">
        <a className = "btn btn-secondary btn-sm" data-toggle = "collapse"  href = "#streamnotify2" role = "button" 
           aria-expanded = "true" aria-controls = "streamnotify" id = "statslinknotify">Metrics
        </a>
        <ul class = "pagination float-right">
          <button class= "btn btn btn-sm ml-2" type="button " onClick = {() => {
              this.setMetrics()
          }} >
          Refresh
          </button>
        </ul>      
      </div>
      <div className = "collapse" id = "streamnotify2">
      <div class= "card-body">
      <ul class="list-group ">
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Active Jobs</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.active_jobs}</span>
            </li>
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Active Subscribers</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.active_subscribers}</span>
            </li>
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Etl Jobs</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.etl_jobs}</span>
            </li>
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Cron Jobs</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.cron_jobs}</span>
            </li> 
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Build Time</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.build_time}</span>
            </li>  
         </ul>
      </div>
      </div>
  </div>
  );}
  }       
}
 
export default Metrics;
 

