import React from 'react';
//Bootstrap and jQuery libraries
import 'bootstrap/dist/css/bootstrap.min.css';
import 'jquery/dist/jquery.min.js';
//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import { fetch_api_call } from '../utils/GraphQl';

class CurrentTime extends React.Component {
  constructor(props){
    super(props); 
    this.state  = { currentime: '' }    
  }

  async fetchEtlFlowJobs() {
    const GRAPHQL_URL = this.props.GRAPHQL_URL
    
    var query =  ` query {  currentime {  current_time  } } `
    const response = await fetch_api_call(GRAPHQL_URL,query)

    const {data} = await response.json();
    return data;    
  }


  componentDidMount() {
    this.fetchEtlFlowJobs().then(({currentime}) => {
      this.setState(prevState => ({
        currentime: currentime.current_time
      }))
    })
 }
  
 render(){
    return (
         <li className ="nav-item">
             <a className = "nav-link disabled">
                {this.state.currentime} 
             </a>
         </li>
    );
  }
}
export default CurrentTime;